<template>
    <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
            <v-btn color="primary"  dark v-on="on"><v-icon >add</v-icon></v-btn>
        </template>
        <v-card >
            <v-form
                    @submit="add_cat"

            >
                <v-toolbar flat color="indigo" dark>
                    <v-toolbar-title> </v-toolbar-title>

                </v-toolbar>
                <v-container>


                    <v-col
                            cols="12"

                    >
                        <v-text-field
                                id="name"
                                v-model="name"
                                color="indigo"
                                :rules="Rules"
                                label=""
                                required
                        ></v-text-field>
                    </v-col>

                    <v-col
                            cols="12"

                    >
                        <v-text-field
                                id="values"
                                v-model="values"
                                hint="      "
                                counter
                                color="indigo"
                                :rules="Rules"
                                label=" "
                                required
                        ></v-text-field>
                    </v-col>


                    <v-btn class="ma-2" outlined color="indigo" type="submit"> </v-btn>

                    <v-btn class="ma-2" outlined color="indigo" @click="dialog=false"  ></v-btn>






                </v-container>
            </v-form>
        </v-card>
    </v-dialog>


</template>

<script>
    // import  { HTTP} from "../main";
    export default {
        name: "NewCategory",
        data() {
            return {
                dialog:false,
                name:'',
                values:'',


                Rules: [
                    v => !!v || ' ',
                ],



            }
        },
        methods: {
            add_cat() {

                let cat = {
                    "name": this.name,
                    "values": this.values

                };
                this.name = null;
                this.values = null;
                alert(JSON.stringify(cat));
                this.$store.dispatch('add_new_cat', cat);
                this.dialog = false;
            },

        }
    }

</script>
